.attraction-detail {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.back-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
}

.back-btn:hover {
    background-color: #0056b3;
}

.attraction-detail h1 {
    font-size: 36px;
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

.attraction-detail img {
    width: 100%;
    max-height: 800px;
    object-fit: none;
    border-radius: 10px;
    margin-bottom: 20px;
}

.attraction-detail p {
    font-size: 18px;
    line-height: 1.6;
    color: #555;
    margin-bottom: 20px;
}

.modal__gallery {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    overflow-x: auto;
}

.modal__gallery-item {
    width: 150px;
    height: 275px;
    object-fit: cover;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.modal__gallery-item:hover {
    transform: scale(1.1);
}

.modal__reviews {
    margin-top: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.modal__reviews h3 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
}

.reviews-list {
    max-height: 300px;
    overflow-y: auto;
    margin-bottom: 20px;
}

.review {
    border-bottom: 1px solid #ddd;
    padding: 10px 0 0 30px;
}

.review__author {
    font-weight: bold;
    color: #333;
}

.review__text {
    font-size: 16px;
    color: #555;
    margin: 5px 0;
}

.review__rating {
    color: #ffc107;
    font-size: 16px;
}

.review__delete {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

.review__delete:hover {
    background-color: #cc0000;
}

.review-form {
    margin-top: 20px;
}

.review-form input,
.review-form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.review-form textarea {
    height: 100px;
    resize: vertical;
}

.rating {
    display: flex;
    gap: 5px;
    margin-bottom: 10px;
}

.rating__star {
    font-size: 24px;
    color: #ccc;
    cursor: pointer;
    transition: color 0.3s ease;
}

.rating__star.selected {
    color: #ffc107;
}

.review-form button {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.review-form button:hover {
    background-color: #218838;
}

.fullscreen-gallery {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.fullscreen-image {
    max-width: 90%;
    max-height: 90%;
    object-fit: contain;
}

.close-fullscreen {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.gallery-prev-fullscreen,
.gallery-next-fullscreen {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.gallery-prev-fullscreen {
    left: 20px;
}

.gallery-next-fullscreen {
    right: 20px;
}

@media (max-width: 768px) {
    .attraction-detail h1 {
        font-size: 28px;
    }

    .attraction-detail img {
        max-height: 300px;
    }

    .modal__gallery-item {
        width: 100px;
        height: 75px;
    }

    .modal__reviews h3 {
        font-size: 20px;
    }

    .review__text {
        font-size: 14px;
    }

    .review-form input,
    .review-form textarea {
        font-size: 14px;
    }

    .rating__star {
        font-size: 20px;
    }

    .review-form button {
        font-size: 14px;
    }
}

@media (max-width: 450px) {
    .attraction-detail {
        padding: 10px;
    }

    .attraction-detail h1 {
        font-size: 24px;
    }

    .attraction-detail img {
        max-height: 200px;
    }

    .modal__gallery-item {
        width: 80px;
        height: 60px;
    }

    .modal__reviews h3 {
        font-size: 18px;
    }

    .review__text {
        font-size: 12px;
    }

    .review-form input,
    .review-form textarea {
        font-size: 12px;
    }

    .rating__star {
        font-size: 18px;
    }

    .review-form button {
        font-size: 12px;
    }
}
#map {
    width: 100%;
    height: 400px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    #map {
        height: 300px;
    }
}

@media (max-width: 450px) {
    #map {
        height: 200px;
    }
}
