.info__main {
    display: flex;
}
@media (max-width: 450px) {
    .info__main {
        display: block;
        margin-top: 50px;
    }
}
.info h2 {
    color: #000000;
}
@media (max-width: 450px) {
    .info h2 {
        font-size: 30px;
    }
}
.info__img {
    padding: 100px 0px 100px 200px;
    opacity: 1;
    height: 500px;
    width: 700px;
}
@media (max-width: 450px) {
    .info__img {
        padding: 0 10px;
        height: 300px;
        width: 350px;
        margin-left: 70px;
    }
}
.info__img:active {
    opacity: 4;
}
.info__box {
    padding: 100px 0px 100px 150px;
    max-width: 750px;
}
@media (max-width: 450px) {
    .info__box {
        padding: 0px 60px;
    }
}
.info__title {
    padding-top: 30px;
    font-size: 36px;
    font-weight: 600;
}
@media (max-width: 450px) {
    .info__title {
        font-size: 30px;
    }
}
.info__text {
    padding: 50px 0 0 0;
    font-size: 24px;
    font-weight: 300;
}
@media (max-width: 450px) {
    .info__text {
        font-size: 20px;
    }
}
.info__btn {
    padding: 30px 50px;
    font-size: 30px;
    margin-top: 50px;
    border: 0;
    background: #59cab0;
    color: white;
    transition: all 0.9s cubic-bezier(0.19, 1, 0.22, 1);
    box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
}
@media (max-width: 450px) {
    .info__btn {
        padding: 10px 30px;
    }
}
.info__btn a {
    color: #000;
}
.info__btn:hover {
    border: 4px solid;
    box-shadow:
        inset 0 0 20px rgba(255, 255, 255, 0.5),
        0 0 20px rgba(255, 255, 255, 0.2);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    text-shadow: 1px 1px 2px #427388;
    background-color: white;
    color: #000000;
}
.info__box-img {
    display: flex;
    justify-content: center;
}
